#tfs_css_content {
  td {
    font-size: 15px;
    line-height: 2em;
  }
}
.csd_ext_my_subscriptions {
  .subs_content {
    width: 100% !important;
    p {
      line-height: 1.8em !important;
    }

  }
  table {
    width: 100%;
    button {
      padding: 1px 11px;
    }
    .csd_ext_now_button {
      margin-bottom: 12px;
    }
  }
}
#csd_ext_modal {
  .csd_ext_modal_content {
    width: 80%;
    margin: auto;
    iframe {
       margin: 0;
    }
  }
  .modal-response-btn-container {
    margin-top: 50px;
  }
  .csd_ext_response_content {
    padding-left: 120px;
    padding-bottom: 34px;
    padding-top: 20px;
    &.csd_ext_change_status_content {
      padding-top: 0;
      padding-bottom: 0;
      .csd_ext_change_status_menu {
        width: 100%;
        td {
          text-align: left;
          padding-left: 10px !important;
          button {
            position: static !important;
          }
        }
      }
    }
  }
  .tfs_css_preloader {
    padding-top: 55px;
  }
  .tfs_css_prompt_form {
    text-align: left;
    input {
      width: 100%;
      box-sizing: border-box;
    }
    label {
      clear: both;
      display: block;
      &.input_label {
        margin-top: 1em;
      }
    }
  }
  .csd_ext_button_container {
    text-align: center;
    clear: both;
    .csd_ext_button {
      position: static;
    }
  }
}
.csd_ext_strike {
  text-decoration: line-through;
}
.tfs_css_success_msg_modal {
  &.csd_ext_success_msg_modal {
    background-color: $success;
    background-image: none;
    &:after {
      content: "\f00c";
      font-family: FontAwesome;
      font-size: 50px;
      color: $success-font;
      z-index: 1;
    }
  }
  &.csd_ext_notice_msg_modal {
    background-color: $notice;
    background-image: none;
    &:after {
      content: "\f05a";
      font-family: FontAwesome;
      font-size: 50px;
      color: $notice-font;
      z-index: 1;
    }
  }
}
.tfs_css_error_msg_modal {
  &.csd_ext_error_msg_modal {
    background-color: $error;
    background-image: none;
    &:after {
      content: "\f057";
      font-family: FontAwesome;
      font-size: 50px;
      color: $error-font;
      z-index: 1;
    }
  }
}
.short-text {
  padding-top: 20px;
}
.csd_ext_renewal_price_span {
  text-decoration: line-through;
}
@media only screen and (max-width: 768px) {
  .csd_ext_my_subscriptions {
    tr {
      display: block;
      td:last-of-type {
        display: block;
        button {
          width: 100%;
          padding: 10px;

        }
      }
    }
  }
}